.swiper-container {
  width: 100%;
  height: 100%;
  padding: 0.5rem 0.25rem;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  width: 2.75rem;
  height: 2.75rem;
  background-color: $secondary;
  border-radius: 50%;
  color: $white;
  cursor: pointer;
  z-index: 20;
  &:hover {
    background-color: $primary;
  }
  &:focus {
    box-shadow: 0 0 0 0.2rem rgba($secondary, 0.5);
    outline: 0;
  }

  &--next {
    left: -0.5rem;
    @include mobile-s {
      left: 0.25rem;
    }
  }
  &--prev {
    right: -0.5rem;
    @include mobile-s {
      right: 0.25rem;
    }
  }
}

.silder {
  position: relative;
  margin: -0.5rem -0.875rem;
}

.silder-card {
  // width: 100%;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 0.25rem;
  &__img {
    margin: 0.25rem;
    min-height: 8rem;
    @include bg-full(cover);
  }
  &__body {
    position: relative;
    padding: 1.75rem 0;
  }
  &__thumbnail {
    margin: -3.125rem auto 0.25rem;
  }
}