.card {
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 0.25rem;
  &__img {
    min-height: 8rem;
    @include bg-full(cover);
  }
  &__body {
    padding: 0.5rem 1rem;
    @include pad {
      padding: 0.5rem 1.125rem;
    }
  }
  &__btn {
    display: block;
    padding: 0.75rem;
    width: 100%;
    background: $white;
    text-align: center;
    @include transition(0.25s);
    &:hover {
      background: $primary;
      color: $white;
    }
  }
}